import React, {useRef, useState, useEffect, useCallback} from 'react';
import {shuffle} from 'utils/shuffle.js';
import {multiplayer} from './multiplayer.js';
import {fetchSongMetadata} from 'stems/getStems.js';
import {getDropAddress} from 'zpore/getDropAddress.js';
import {getZporeRemixes} from 'zpore/getZporeRemixes.js';
import {getAttributeMapping} from 'erc721/attributes.js';


const MIN_LEAK = {
    0: 3,
    1: 3,
    2: 0,
    3: 5,
    4: 11
};
const fetchAllLeaks = async () => {
    let drops  = await getDropAddress();
    let songs = await fetchSongMetadata(undefined, true);
    let leaks = [];
    for (let {songId, dropAddress} of drops) {
        let song = songs.find(x => x.songId === songId);
        let minTokenId = MIN_LEAK[songId] || 0;
        let remixes = await getZporeRemixes(songId, dropAddress);
        remixes = remixes.filter(
            x => parseInt(x.tokenId) >= minTokenId);
        remixes
            .forEach(x => {
                x.song = song;
                x.song.dropAddress = dropAddress;
                x.attributes = getAttributeMapping(x.metadata.attributes);
        });
        leaks = [... leaks, ... remixes];
    }
    shuffle(leaks);
    return leaks;
};

export const useLeaks = ({playStatus, songId}) => {

    let [leakTime, setLeakTime] = useState(5000);
    let [leaks, setLeaks] = useState([]);
    let [nextLeak, setNextLeak] = useState();
    let [loadedLeak, setLoadedLeak] = useState();

    let leakCounter = useRef(0);
    let interval = useRef();

    // when available -> true, and timer gets to 0, we show
    // the user a new leak
    let available = useRef(true);

    useEffect(() => {
        fetchAllLeaks().then(
            setLeaks);
    }, []);

    useEffect(() => {
        clearInterval(interval.current);
        if (playStatus) {
            interval.current = setInterval(() => {
                if (available.current) {
                    let _nextLeak = leaks[leakCounter.current % leaks.length];
                    let url = _nextLeak.metadata.animation_url;
                    _nextLeak.downloadedAudio = new Promise(async (resolve) => {
                        let resp = await fetch(url, {method: "GET"});
                        let json = await resp.arrayBuffer();
                        let blob = new Uint8Array(json);
                        resolve(blob);
                    });
                    setNextLeak(_nextLeak);
                    available.current = false;
                    leakCounter.current = leakCounter.current+1;
                }
            }, leakTime);
        } else {
        }
    }, [playStatus, leaks, setNextLeak, leakTime]);

    const chooseLeak = useCallback(() => {
        let leak = nextLeak;
        available.current = true;
        setLoadedLeak(leak);
        setNextLeak(undefined);
        multiplayer(songId, leak);
    }, [setNextLeak, setLoadedLeak, songId, nextLeak]);

    const hideLeaks = useCallback((time=5000) => {
        setNextLeak(undefined);
        setLeakTime(time);
        setLoadedLeak(undefined);
        available.current = true;
        leakCounter.current = leakCounter.current+1;
    }, [setLeakTime, setNextLeak, setLoadedLeak]);

    return {leaks, chooseLeak, nextLeak, loadedLeak, hideLeaks};
};

