import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import UnknownProgressBar from 'components/progressbar/UnknownProgressBar.js';


const SavingProgressInfo = ({
    hide,
    download,
    transaction,
    downloading,
    isWaitingForSignature
}) => { 
    if (downloading) {
        return (
            <div className="download-test">
              <UnknownProgressBar/>
            </div> 
        );
    } else if (isWaitingForSignature) {
        return (
            <div className="waiting download-test">
              We are transmitting your remix on-chain.
              <div className="test-net">
                This is on a test-network, as we develop this.
              </div>
              <UnknownProgressBar/>
            </div> 
        );
    } else {
        return (<div className="download-test">
         <span>Download your remix <a target="_blank" href={"https://zequencer.mypinata.cloud/ipfs/" + download}>here</a>. We're still working on some things :) </span>
              <br/>

                  <div
                    onClick={hide}
                    className="px-3 py-1 text-md ok-button">
                    
                    OK <svg height="13" width="16"><path d="M14.293.293l1.414 1.414L5 12.414.293 7.707l1.414-1.414L5 9.586z"></path></svg>
                </div>
                </div>);
    }
  return (<div></div>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SavingProgressInfo);
