import React, { useEffect, useCallback, useState} from 'react';
import BlendAnimation from './BlendAnimation.js';
import CloseIcon from 'components/svg/CloseIcon.js';
import ENSName from 'components/ens/ENSName.js';
import {connect} from 'react-redux';
import './leak-controller.scss';
import CurveDiv from 'components/ux/CurveDiv.js';
import {walk, curve, relative, cut, radius} from 'components/ux/dsl.js';
import {getZorb} from 'utils/getZorb.js';

const LeakController = ({zorbId, hideLeaks, leak, playerRef}) => { 
    let [zorbs, setZorbs] = useState([]);

    useEffect(() => {
        let _zorbId = leak.attributes["zorb"];
        getZorb(_zorbId).then((a) =>
            getZorb(zorbId).then(b => setZorbs([a,b])));
        }, [leak, setZorbs]);

    const DROP_ALGO = [
        [],
        [
            radius(3),
        ],
        [
            radius(5),
        ],
        [
            radius(5),
            walk(50),
            cut(10, 15, 30)
        ],
    ];

    const revert = useCallback(() => {
        playerRef.current.contentWindow.postMessage({
            type: "revert-stem",
            body: ""
        }, "*");
        hideLeaks();
    }, [playerRef]);

    let remixCreator = leak.mintInfo.originatorAddress;
    return (
        <CurveDiv
          relative={false}
          svgFilter={{def: (
              <defs><filter id='shadow' color-interpolation-filters="sRGB">
                      <feDropShadow dx="5" dy="3" stdDeviation="3" flood-opacity="0.5"/>),
                      
                    </filter>
              </defs>),
                     name: "url(#shadow)"
                    }}
          algo={DROP_ALGO} radius={24} fill="#ffffff33" stroke="transparent" className="leak-controller fixed z-30">
          <div className="text-xs z-30 pt-3 pb-1 pl-4 pr-1">
            <div className="flex">
              <div>{leak.song.title} </div>
              <div className="px-2 py-1 bg-white text-black ml-2 text-xs rounded-full leak-label">leak</div>
            </div>
            <div className="flex text-zinc-100">
              {leak.song.artist}
            </div>
            <div className="flex mt-2">
              <div className="flex mr-3 zorbs">
                {zorbs.map((x, i) => <img src={x} className={"w-8 " + (i == 1 ? " second" : "")}/>)}
              </div>
              <ENSName className="text-zinc-500" address={remixCreator}/>
            </div>
          </div>
          <BlendAnimation leak={leak}/>
          <CloseIcon
            onClick={revert}
            className="close-icon">
          </CloseIcon>
        </CurveDiv>
    );
};

const mapStateToProps = (state, props) => {
  return {
      playerRef: state.spore.playerRef
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(LeakController);
