import React, { useEffect, useCallback, useState} from 'react';
import {truncate} from 'components/spore/AudioPlayer.js';
import { useEnsName } from 'wagmi';
import './ens.scss';

const ENSName = ({address, className}) => { 
    const { data, isError, isLoading } = useEnsName({
        address
    });

    if (isLoading) return '';

    return (
        <span className={"ens-name " + className}>
          {data === null ? truncate(address) : data}
        </span>);
};

export default ENSName;
