import React, { useRef, useEffect, useCallback, useState} from 'react';
import {useInterval} from 'components/hooks/useInterval.js';
import {connect} from 'react-redux';

const WIDTH = 150;
const BlendAnimation = ({leak}) => { 
    
    let leakCounter = useRef(0);
    let blendStartTime = useRef(0);
    let [percentage, setPercentage] = useState();
    const onTick = useCallback(() => {
        let time = new Date().getTime() - blendStartTime.current 
        setPercentage(100*Math.min(1, time / 5000));
    }, [setPercentage]);

    useEffect(() => {
        blendStartTime.current = new Date().getTime();
        leakCounter.current++;
    }, [leak]);

    useInterval(onTick, 50);

    return (
        <div
          style={{width: WIDTH}} className={"ml-4 h-2 bg-zinc-500 p-0.5 rounded-full " + (percentage === 100 ? "  opacity-0" : "")}>
          <div style={{width: (WIDTH-4)*percentage/100}}
               className={"h-1 bg-white overflow-hidden" + (leakCounter.current % 2 === 0 ? " ml-auto" : "")}/>
        </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(BlendAnimation);
