export const getAttributeValue = (attributes, type) => {
    let obj = attributes.find(x => x.trait_type === type);
    let value = obj && obj.value; 
    let parsed = parseFloat(value);
    if (!isNaN(parsed)) {
        return parsed;
    }
    return value;
};

export const getAttributeMapping = (attributes) => {
    let keys = attributes.map(x => x.trait_type);
    let obj = {};
    for (let key of keys) {
        obj[key] = getAttributeValue(attributes, key);
    }
    return obj;
};
