import React, { useEffect, useRef, useCallback, useState} from 'react';
import {TYPE_TO_NAME} from 'constants/stem-types.js';
import {getStemFromURI} from 'stems/getStems.js';
import {connect} from 'react-redux';
import './tooltip.scss';

let cache = {};
const ZorbSampleTooltip = ({tooltip, setTooltip}) => { 
    let timer = useRef();
    useEffect(() => {
        window.addEventListener("message", onMessage);
        return () => window.removeEventListener("message", onMessage);
    }, [setTooltip]);

    const onMessage = useCallback((e) => {
        if (e.data.type === "sample-engaged") {
            let uri = e.data.body;
            setTooltip(undefined);
            clearTimeout(timer.current);
            if (cache[uri]) {
                setTooltip(cache[uri]);
            } else {
                getStemFromURI(uri).then((e) => {
                    cache[uri] = e;
                    setTooltip(e);
                });
            }
            timer.current = setTimeout(() => {
                setTooltip(undefined);
            }, 4000);
        }
    }, [setTooltip]);

    if (!tooltip) {
        return '';
    }

    let type = TYPE_TO_NAME[tooltip.types[0]];
    return (<div className="px-4 py-2 text-sm w-96 bg-zinc-100	 text-black zorb-sample-tooltip z-30 fixed bottom-0 left-0 right-0 mx-auto">
              {tooltip && <div className="flex">
                            <span className="mr-2">{type === "Instruments" ? "Synths" : type}:</span>
                            <span className="mr-1">{tooltip.artist}</span> - 
                            <span className="ml-2 text-zinc-500">{tooltip.title}</span> 
                            <span className="rounded-full bg-zinc-500 px-2 text-xs py-1 ml-auto">#{tooltip.stemId}</span>
                          </div>}
            </div>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ZorbSampleTooltip);
