import {ZporeDropCreated} from 'decentralized/EthereumEvents.js';
import BackendQuery from 'decentralized/BackendQuery.js';
import {STEMS_CONTRACT} from 'stems/contract.js';

export const getDropAddress = async (songId) => {
    let query = new BackendQuery(STEMS_CONTRACT)
        .from(ZporeDropCreated)
        .select("dropAddress")
        .select("songId");

    if (songId !== undefined) {
        query = query.where("songId", parseInt(songId));
    }
    let results = await query.executeQuery();
    if (songId  !== undefined) {
        return results[0].dropAddress;
    } else {
        return results;
    }
};
