import Notes from 'utils/Notes.js';

export const getTransposeDiff = (a, b) => {
    let [noteA, keyA] = a.key.split(" ");
    let [noteB, keyB]= b.key.split(" ");

    let noteA1 = Notes.fromString(noteA+'3');
    let transposeA = Notes.fromString(noteA+'3').getTranspose();
    let transposeB = Notes.fromString(noteB+'3').getTranspose();

    if (transposeA > 6) {
        transposeA = transposeA - 12;
    }
    if (transposeB > 6) {
        transposeB = transposeB - 12;
    }

    return transposeA - transposeB;
    // okay we can 
};
 
