import {fetchSongMetadata} from 'stems/getStems.js';
import {getZorb} from 'utils/getZorb.js';
import {getTransposeDiff} from 'stems/getTransposeDifference.js';
import {getZporeRemixes} from 'zpore/getZporeRemixes.js';
import {getAttributeMapping} from 'erc721/attributes.js';
import {INSTRUMENTS, BASS, DRUMS} from 'constants/stem-types.js';

let lastArgs = {};
window.execute = () => {
    multiplayer(lastArgs.songId, lastArgs.leak);
};
export const multiplayer = async (songId, leak) => {
    lastArgs = {songId, leak};
    // given songId we can grab that
    // current osng
    let song = await fetchSongMetadata(window.songId, true);
    let otherSong = leak.song; 
    let remix = leak;

    let dropAddress = otherSong.dropAddress;
    let tokenId = parseInt(leak.tokenId);

    let songOffset = parseInt(song.attributes["start-time"])/1000;
    let otherSongOffset = parseInt(otherSong.attributes["start-time"] || 33)/1000;


    let {detune, bpm, zorb} = leak.attributes;
    let leakBPM = bpm;
    let bpms = [bpm, 2*bpm, bpm/2];

    bpms.sort((a,b) => Math.abs(song.bpm - a) - Math.abs(song.bpm - b));
    bpm = bpms[0];
    let transposeOff = 0;
    if (bpm < leak.attributes.bpm) {
        if (Math.abs(leak.attributes.bpm - song.bpm) < 35) {
            bpm = bpms[1];
            transposeOff = -12;
        }
    }


    if (song.bpm > 80 && song.bpm < 110) {
        // then we need to never allow hyper fast bpms which are just insane
        if (leak.attributes.bpm > 90) {
            bpm = leak.attributes.bpm;
            if (leak.attributes.bpm >= 110) {
                transposeOff = -12;
            }
        }
    }

    let bpmRatio = otherSong.bpm / bpm;
    let adjustedOffset = bpmRatio * otherSongOffset; // how much it has to pitch down from OG other song
    let offsetDifference = otherSongOffset - songOffset;

    // how many semitones we need to pitch this to match this current song
    // note: this remix is from this same song so it
    let semitones = detune / 100; 

    // say this sample is +6st then we need to pitch it down -6st
    let transpose = getTransposeDiff(song, otherSong) - semitones;
    if (transpose >= 12) {
        transpose = transpose % 12;
    } else if (transpose >= 6) {
        transpose -= 12;
    } else if (transpose < -6) {
        transpose += 12;
    }

    if (bpm < otherSong.bpm && bpm)  {
    }
    transpose += transposeOff;

    let id = remix.metadata.animation_url;
    let sound = await remix.downloadedAudio; // wait for promise

    // the information needed by the player to load this correctly
    let payload = {
        sound: sound,
        id,
        transpose,
        onset: adjustedOffset,
        volume: 0.5,
        bpm: bpm,
        contractAddress: dropAddress,
        tokenId,
        leakBPM,
        stemTypes: [INSTRUMENTS, BASS, DRUMS] // what to replace
    };

    // send this to the player and have it replace the instrumental
    window.playerRef.current.contentWindow.postMessage({
        type: "load-stem",
        body: payload
    }, "*");

    getZorb(zorb).then(
        uri => {
            let starter = 'base64,';
            if (uri.indexOf(starter) >= 0) {
                let str = atob(uri.slice(uri.indexOf(starter) + starter.length));
                window.playerRef.current.contentWindow.postMessage({
                    type: "incoming-zorb",
                    body: str
                }, "*");
            }
        });

};

window.multiplayer = multiplayer;
